<template>
  <a-layout style="height: 100vh; " >
    <a-layout-header style="padding-top: 70px;">

    </a-layout-header>

    <a-layout-content style="padding-right: 300px; margin: auto;">
      <a-grid :cols="2" :colGap="120" :rowGap="16" class="grid-demo-grid">
        <a-card hoverable class="card" :style=card_style>
          <div :style=card_div_style>
            <span :style=card_content_style>
              <a-avatar :style=card_icon_style>
                <icon-apps />
              </a-avatar>
              <a-typography-text>申请开课</a-typography-text>
            </span>
            <!-- <a-link href="/#/teacher/apply_for_course">进入</a-link> -->
            <a-link @click="link(1)">进入</a-link>
          </div>
        </a-card>

        <a-card hoverable :style=card_style>
          <div :style=card_div_style>
            <span :style=card_content_style>
              <a-avatar :style=card_icon_style>
                <icon-pen-fill />
              </a-avatar>
              <a-typography-text>学生打分</a-typography-text>
            </span>
            <!-- <a-link href="/#/teacher/giveScoreForm">进入</a-link> -->
            <a-link @click="link(2)">进入</a-link>
          </div>
        </a-card>

        <a-card hoverable :style=card_style>
          <div :style=card_div_style>
            <span :style=card_content_style>
              <a-avatar :style=card_icon_style>
                <icon-calendar-clock />
              </a-avatar>
              <a-typography-text>我的课程</a-typography-text>
            </span>
            <!-- <a-link href="/#/teacher/courses">进入</a-link> -->
            <a-link @click="link(3)">进入</a-link>
          </div>
        </a-card>

        <a-card hoverable :style=card_style>
          <div :style=card_div_style>
            <span :style=card_content_style>
              <a-avatar :style=card_icon_style>
                <icon-storage />
              </a-avatar>
              <a-typography-text>我的申请</a-typography-text>
            </span>
            <!-- <a-link href="/#/teacher/applications">进入</a-link> -->
            <a-link @click="link(4)">进入</a-link>
          </div>
        </a-card>
        
        <a-card hoverable :style=card_style>
          <div :style=card_div_style>
            <span :style=card_content_style>
              <a-avatar :style="{ marginRight: '10px', backgroundColor: '#ff5722' }">
                <icon-export />
              </a-avatar>
              <a-typography-text>退出登录</a-typography-text>
            </span>
            <!-- <a-link href="/#/teacher/applications">进入</a-link> -->
            <a-link href="/">退出</a-link>
          </div>
        </a-card>

      </a-grid>
    </a-layout-content>
  </a-layout>
</template>


<script>

export default {
  data() {
    return {
      card_style: { width: '25vw', marginBottom: '20px' },
      card_div_style: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
      },
      card_content_style: { display: 'flex', alignItems: 'center', color: '#1D2129' },
      card_icon_style: { marginRight: '10px', backgroundColor: '#165DFF' },
    }
  },

  methods:{
    link(index){
      switch (index) {
        case 1:
          this.$router.push({path: "/teacher/apply_for_course"})
          break;
        case 2:
          this.$router.push({path: "/teacher/giveScoreForm"})
          break;
        case 3:
          this.$router.push({path: "/teacher/courses"})
          break;
        case 4:
          this.$router.push({path: "/teacher/applications"})
          break;
      
        default:
          break;
      }
    }
  }
}

</script>

